<template>
    <section>
        <!---->
        <el-collapse v-model="activeNames">
            <el-collapse-item title="" name="1">
                <el-form ref="form" :model="form" @submit.prevent="onSubmit" style="margin:10px;">
                    <el-form :model="filters">
                        <el-row>
                            <el-col :xs="12" :sm="12" :md="12" :lg="5" style="margin-left: 12px;">
                                <el-form-item>
                                    <el-input v-model="filters.strPlanId" placeholder="ID"
                                              style="width: 160px;"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :xs="10" :sm="10" :md="10" :lg="5">
                                <el-form-item>
                                    <el-input v-model="filters.strMaintainId" placeholder="ID"
                                              style="width: 160px;"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :xs="12" :sm="12" :md="12" :lg="5" style="margin-left: 12px;">
                                <el-form-item>
                                    <el-input v-model="filters.equipmentCategory" placeholder="ID"
                                              style="width: 160px;"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :xs="10" :sm="10" :md="10" :lg="5">
                                <el-form-item>
                                    <el-input v-model="filters.equipmentId" placeholder="ID"
                                              style="width: 160px;"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :xs="12" :sm="12" :md="12" :lg="14">
                                <el-form-item label-width="80px" label="" class="postInfo-container-item">
                                    <el-date-picker
                                            v-model="filters.executeTime"
                                            type="datetimerange"
                                            :picker-options="pickerOptions2"
                                            placeholder=""
                                            align="right">
                                    </el-date-picker>
                                </el-form-item>
                            </el-col>
                            <el-col :xs="10" :sm="10" :md="10" :lg="6" style="margin-left: 20px;">
                                <el-form-item label=":">
                                    <el-radio-group v-model="filters.isCycle">
                                        <el-radio class="radio" :label="1"></el-radio>
                                        <el-radio class="radio" :label="0"></el-radio>
                                    </el-radio-group>
                                </el-form-item>
                            </el-col>
                            <el-col :xs="12" :sm="12" :md="12" :lg="8" v-if="filters.isCycle" style="margin-left: 12px;">
                                <el-form-item label="()">
                                    <el-select v-model="filters.cycleDay" multiple placeholder="">
                                        <el-option
                                                v-for="item in cycleDays"
                                                :key="item.value"
                                                :label="item.label"
                                                :value="item.value">
                                        </el-option>
                                    </el-select>
                                </el-form-item>
                            </el-col>
                            <el-col :xs="12" :sm="12" :md="12" :lg="6">
                                <el-form-item style="margin-left: 10px;">
                                    <el-button type="primary" v-on:click="getPlans" icon="search"></el-button>
                                </el-form-item>
                            </el-col>
                        </el-row>
                    </el-form>
                </el-form>
            </el-collapse-item>
        </el-collapse>

        <div class="panel">
            <div class="panel-title">
                <span v-text="panelTitle"></span>
                <div class="fr">
                    <el-button @click.stop="on_refresh" size="small">
                        <i class="fa fa-refresh"></i>
                    </el-button>
                    <el-button type="primary" @click="handleAdd" icon="plus" size="small"></el-button>
                </div>
            </div>
            <div class="panel-body">
                <!---->
                <el-table :data="plans" highlight-current-row v-loading="listLoading" @selection-change="selsChange">
                    <el-table-column type="selection" width="55">
                    </el-table-column>
                    <el-table-column type="index" width="60">
                    </el-table-column>
                    <el-table-column prop="strPlanId" label="ID" width="120" sortable>
                    </el-table-column>
                    <el-table-column prop="strMaintainId" label="ID" width="120" sortable>
                    </el-table-column>
                    <el-table-column prop="executeTime" label="" width="120" sortable>
                    </el-table-column>
                    <el-table-column prop="isCycle" label="" width="120" :formatter="formatCycle">
                    </el-table-column>
                    <el-table-column prop="cycleDay" label="">
                    </el-table-column>
                    <el-table-column prop="description" label="">
                    </el-table-column>
                    <el-table-column prop="equipmentCategory" label="ID" sortable>
                    </el-table-column>
                    <el-table-column prop="equipmentId" label="ID" sortable>
                    </el-table-column>
                    <el-table-column label="" width="150">
                        <template scope="scope">
                            <el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
                            <el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)">
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>

                <!---->
                <el-col :span="24" class="toolbar">
                    <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0" class="fl"></el-button>
                    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="listQuery.curPage" :page-sizes="[10,20,30, 50]" :page-size="listQuery.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total" class="fr">
                    </el-pagination>
                </el-col>
            </div>
        </div>

        <!---->
        <el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
            <el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
                <el-form-item label="ID" prop="strPlanId" style="width: 292px;">
                    <el-input v-model="editForm.strPlanId" auto-complete="off" disabled></el-input>
                </el-form-item>
                <el-form-item label="ID" prop="strMaintainId" style="width: 292px;">
                    <el-input v-model="editForm.strMaintainId" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="executeTime">
                    <el-date-picker
                            v-model="editForm.executeTime"
                            type="datetime"
                            placeholder=""
                            align="right"
                            :picker-options="pickerOptions1">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="" prop="isCycle">
                    <el-radio-group v-model="editForm.isCycle">
                        <el-radio class="radio" :label="1"></el-radio>
                        <el-radio class="radio" :label="0"></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="()" prop="cycleDay">
                    <el-input-number v-model="editForm.cycleDay" @change="handleChange" v-if="editForm.isCycle" :min="1"
                                     :max="30"></el-input-number>
                </el-form-item>
                <el-form-item label="" prop="description" style="width: 450px;">
                    <el-input
                            type="textarea"
                            :rows="2"
                            placeholder=""
                            v-model="editForm.description">
                    </el-input>
                </el-form-item>
                <el-form-item label="ID" prop="equipmentCategory" style="width: 292px;">
                    <el-input v-model="editForm.equipmentCategory" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="ID" prop="equipmentId" style="width: 292px;">
                    <el-input v-model="editForm.equipmentId" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="editFormVisible = false"></el-button>
                <el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
            </div>
        </el-dialog>

        <!---->
        <el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false">
            <el-form ref="addForm" :model="addForm" label-width="100px" :rules="addFormRules">
                <el-form-item label="ID" prop="strMaintainId" style="width: 292px;">
                    <el-input v-model="addForm.strMaintainId" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="executeTime">
                    <el-date-picker
                            v-model="addForm.executeTime"
                            type="datetime"
                            placeholder=""
                            align="right"
                            :picker-options="pickerOptions1">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="" prop="isCycle">
                    <el-radio-group v-model="addForm.isCycle">
                        <el-radio class="radio" :label="1"></el-radio>
                        <el-radio class="radio" :label="0"></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="()" prop="cycleDay" v-if="addForm.isCycle">
                    <el-input-number v-model="addForm.cycleDay" @change="handleChange" :min="1"
                                     :max="90"></el-input-number>
                </el-form-item>
                <el-form-item label="" prop="description" style="width: 450px;">
                    <el-input
                            type="textarea"
                            :rows="2"
                            placeholder=""
                            v-model="addForm.description">
                    </el-input>
                </el-form-item>
                <el-form-item label="ID" prop="equipmentCategory" style="width: 292px;">
                    <el-input v-model="addForm.equipmentCategory" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="ID" prop="equipmentId" style="width: 292px;">
                    <el-input v-model="addForm.equipmentId" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="addFormVisible = false"></el-button>
                <el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
            </div>
        </el-dialog>
    </section>
</template>

<script>
    import util from '../../common/js/util'
    //import NProgress from 'nprogress'
    import {getPlanListPage, removePlan, batchRemovePlan, editPlan, addPlan} from '../../api/api';

    export default {
        data() {
            return {
                filters: {
                    strPlanId: '',
                    strMaintainId: '',
                    equipmentCategory: '',
                    equipmentId: '',
                    executeTime: '',
                    isCycle: 0,
                    cycleDay: []
                },
                panelTitle: '',
                activeNames: ['1'],
                pickerOptions1: {
                    shortcuts: [{
                        text: '',
                        onClick(picker) {
                            picker.$emit('pick', new Date());
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() + 3600 * 1000 * 24);
                            picker.$emit('pick', date);
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() + 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', date);
                        }
                    }]
                },
                pickerOptions2: {
                    shortcuts: [{
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                            picker.$emit('pick', [start, end]);
                        }
                    }]
                },
                cycleDays: [{
                    value: '1',
                    label: '1'
                }, {
                    value: '2',
                    label: '2'
                }],
                plans: [],
                total: 0,
                listLoading: false,
                sels: [],//

                form: {
                    strPlanId: 0,
                    strMaintainId: '',
                    executeTime: '',
                    isCycle: 0,
                    cycleDay: '',
                    description: '',
                    equipmentCategory: '',
                    equipmentId: ''
                },

                listQuery: {
                    curPage: 1,
                    limit: 20,
                    pageSize: 10,
                    importance: undefined,
                    title: undefined,
                    type: undefined,
                    sort: '+id'
                },

                editFormVisible: false,//
                editLoading: false,
                editFormRules: {
                    strMaintainId: [
                        {required: true, message: 'ID', trigger: 'blur'}
                    ],
                    executeTime: [
                        {required: true, validator: this.checkExecuteTime, trigger: 'blur'}
                    ],
                    equipmentCategory: [
                        {required: true, message: 'ID', trigger: 'blur'}
                    ],
                    equipmentId: [
                        {required: true, message: 'ID', trigger: 'blur'}
                    ]
                },
                //
                editForm: {
                    strPlanId: 0,
                    strMaintainId: '',
                    executeTime: '',
                    isCycle: '',
                    cycleDay: '',
                    description: '',
                    equipmentCategory: '',
                    equipmentId: ''
                },

                addFormVisible: false,//
                addLoading: false,
                addFormRules: {
                    strMaintainId: [
                        {required: true, message: 'ID', trigger: 'blur'}
                    ],
                    executeTime: [
                        {required: true, validator: this.checkExecuteTime, trigger: 'blur'}
                    ],
                    equipmentCategory: [
                        {required: true, message: 'ID', trigger: 'blur'}
                    ],
                    equipmentId: [
                        {required: true, message: 'ID', trigger: 'blur'}
                    ]
                },
                //
                addForm: {
                    strMaintainId: '',
                    executeTime: '',
                    isCycle: 0,
                    cycleDay: '',
                    description: '',
                    equipmentCategory: '',
                    equipmentId: ''
                }

            }
        },
        methods: {
            format: function (time, format) {
                var t = new Date(time);
                var tf = function (i) {
                    return (i < 10 ? '0' : '') + i
                };
                return format.replace(/yyyy|MM|dd|HH|mm|ss/g, function (a) {
                    switch (a) {
                        case 'yyyy':
                            return tf(t.getFullYear());
                            break;
                        case 'MM':
                            return tf(t.getMonth() + 1);
                            break;
                        case 'mm':
                            return tf(t.getMinutes());
                            break;
                        case 'dd':
                            return tf(t.getDate());
                            break;
                        case 'HH':
                            return tf(t.getHours());
                            break;
                        case 'ss':
                            return tf(t.getSeconds());
                            break;
                    }
                })
            },
            checkExecuteTime: (rule, value, callback) => {
                if (!value) {
                    return callback(new Error(''));
                }
                setTimeout(() => {
                    const now = new Date();
                    if (format(value, 'yyyy-MM-dd HH:mm:ss') < format(now, 'yyyy-MM-dd HH:mm:ss')) {
                        callback(new Error(''));
                    } else {
                        callback();
                    }
                }, 500);
            },
            //
            formatCycle: function (row, column) {
                return row.isCycle === 0 ? '' : row.isCycle === 1 ? '' : '';
            },
            handleChange: function (value) {
                console.log(value);
            },
            //
            handleSizeChange(val) {
                this.listQuery.pageSize = val;
                this.getPlans();
            },
            handleCurrentChange(val) {
                this.listQuery.curPage = val;
                this.getPlans();
            },
            //
            on_refresh(){
                this.getPlans();
            },
            //
            getPlans() {
                let para = {
                    curPage: this.listQuery.curPage,
                    pageSize: this.listQuery.pageSize,
                    executeTime: this.filters.executeTime,
                    isCycle: this.filters.isCycle
                };
                this.listLoading = true;
                //NProgress.start();
                getPlanListPage(para).then((res) => {
                    this.total = res.data.total;
                    this.plans = res.data.plans;
                    this.listLoading = false;
                    //NProgress.done();
                });
            },
            //
            handleDel: function (index, row) {
                this.$confirm('?', '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    //NProgress.start();
                    let para = {strPlanId: row.strPlanId};
                    removePlan(para).then((res) => {
                        this.listLoading = false;
                        //NProgress.done();
                        this.$message({
                            message: '',
                            type: 'success'
                        });
                        this.getPlans();
                    });
                }).catch(() => {

                });
            },
            //
            handleEdit: function (index, row) {
                this.editFormVisible = true;
                this.editForm = Object.assign({}, row);
            },
            //
            handleAdd: function () {
                this.addFormVisible = true;
                this.addForm = {
                    strMaintainId: '',
                    executeTime: '',
                    isCycle: 1,
                    cycleDay: '',
                    description: '',
                    equipmentCategory: '',
                    equipmentId: ''
                };
            },
            //
            editSubmit: function () {
                this.$refs.editForm.validate((valid) => {
                    if (valid) {
                        this.$confirm('', '', {}).then(() => {
                            this.editLoading = true;
                            //NProgress.start();
                            let para = Object.assign({}, this.editForm);
                            editPlan(para).then((res) => {
                                this.editLoading = false;
                                //NProgress.done();
                                this.$message({
                                    message: '',
                                    type: 'success'
                                });
                                this.$refs['editForm'].resetFields();
                                this.editFormVisible = false;
                                this.getPlans();
                            });
                        });
                    }
                });
            },
            //
            addSubmit: function () {
                this.$refs.addForm.validate((valid) => {
                    if (valid) {
                        this.$confirm('', '', {}).then(() => {
                            this.addLoading = true;
                            //NProgress.start();
                            let para = Object.assign({}, this.addForm);
                            addPlan(para).then((res) => {
                                this.addLoading = false;
                                //NProgress.done();
                                this.$message({
                                    message: '',
                                    type: 'success'
                                });
                                this.$refs['addForm'].resetFields();
                                this.addFormVisible = false;
                                this.getPlans();
                            });
                        });
                    }
                });
            },
            selsChange: function (sels) {
                this.sels = sels;
            },
            //
            batchRemove: function () {
                var ids = this.sels.map(item => item.strPlanId).toString();
                this.$confirm('', '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    //NProgress.start();
                    let para = {ids: ids};
                    batchRemovePlan(para).then((res) => {
                        this.listLoading = false;
                        //NProgress.done();
                        this.$message({
                            message: '',
                            type: 'success'
                        });
                        this.getPlans();
                    });
                }).catch(() => {

                });
            }
        },
        mounted() {
            this.getPlans();
        }
    }

</script>

<style scoped>

</style>


